import React, {Component} from "react";
import PropTypes from "prop-types";

let tooltipId = 0;

class Tooltip extends Component {

    constructor(props) {
        super(props);
    }

    state = {
        tooltipId: this.props.tooltipId || tooltipId++
    };

    componentDidMount() {
        this.loadModule();
    }

    loadModule() {
        let options =  {
                mouseoverMinWidth: 0,
                triggerEvent: "mouseover"
        };

        if(this.props.maxWidth !== undefined) {
            options.maxWidth = this.props.maxWidth;
        }

        OPL.UI.loadModules(document.getElementById("tooltip-container-" + this.state.tooltipId),[
            {
                path:"core/modules/tooltips",
                options: options
            }
        ]);
    }

    render() {
        return (
            <span id={"tooltip-container-" + this.state.tooltipId}>
                {this.props.children}
            </span>
        );
    }
}

export default Tooltip;